.mask{
    background: rgba(0, 0, 0, 0.75);
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    display:none;
}
.libao-pop{
    position:absolute;
    width:5.29rem;
    height:4.19rem;
    background: url(../images/libao-pop.png) no-repeat;
    background-size: 100% 100%;
    -webkit-background-size:100% 100%;
    left:50%;
    top:2.92rem;
    transform:translateX(-50%);
    -webkit-transform:translateX(-50%);
    z-index:100;
    color:#fff;
    font-size:0.24rem;
    display: none;
    .libao-title{
        width: 4.26rem;
        margin: 0 auto;
        img{
            display: inline-block;
            max-width: 100%;
        }
    }
}
