        img{
            display: inline-block;
            max-width: 100%;
        }
    }
    .libao-wenan{
        width:1.61rem;
        margin:0 auto;
        margin-top: 0.2rem;
        img{
            display: inline-block;
            max-width: 100%;
        }
    }
}
.libao-pop.daojian-introduce{
    padding-top:0.61rem;
    box-sizing:border-box;
    -webkit-box-sizing:border-box;
    .introduce-details{
        font-size: 0.22rem;
        color:#fff;
        width:3.80rem;
        margin:0 auto;
        font-size:0.24rem;
        margin-top:0.34rem;
        line-height:0.32rem;
