.hTip { display: none;}  
@media screen and (orientation: portrait) {.hTip { display: none;} }
@media screen and (orientation: landscape) {.hTip{display: block; width: 100%; height: 100%; position: fixed; z-index: 5000; background-color: rgba(0, 0, 0, 0.7); top: 0; left: 0; text-align: center; color: #FFF; font-size:0.4rem;display: -webkit-box;display: -ms-flexbox;display: -webkit-flex;display: flex;-webkit-box-pack: center;-ms-flex-pack: center;-webkit-justify-content: center;ustify-content: center;-webkit-box-align: center;-ms-flex-align: center;-webkit-align-items: center;align-items: center;}}
@media only screen and (device-width:375px) and (min-height: 812px){
    .pageTwo .inform-introduce{
        top:50%;
        margin-top:-3.5rem;
        transform: translate(-50%,-50%);
        -webkit-transform: translate(-50%,-50%);
    }
    .libao-pop{
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        -webkit-transform: translate(-50%,-50%);
    }
}
@keyframes fadeInUp{
    0%{
        opacity: 0;
        transform: translate3d(0,100%,0);
    }
    100%{
        opacity: 1;
        transform: none;
    }
}
@keyframes rm-zoomin{
    0%{
        opacity: 0;
        transform: scale(0)
    }
    100%{
        opacity: 1;
        transform: scale(1)
    }

}
@keyframes fadeInLeft{
    0%{
        opacity: 0;
        transform: translate3d(-100%,0,0);
    }
    100%{
        opacity: 1;
        transform: none;
    }
}
@keyframes cloudDrift{
    0%{
        transform: translateX(0)
    }
    100%{
        transform: translateX(750px)
    }
}
